.Pokegame {
  display: flex;
  flex: wrap 1;
  background: #70e1f5; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to bottom,
    #ffd194,
    #70e1f5
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
/* @media (max-width: 724px) {
  .Pokegame {
    display: block;
  }
} */

.Pokedex {
  display: flex;
  flex-direction: column;
  border-left: 6px solid black;
  border-right: 6px solid black;
  padding-bottom: 20px;
}
.Pokedex-content {
  padding: 0;
  /* margin-left: 10px;
  margin-right: 10px; */
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.Pokedex-item {
  margin: 0;
  width: auto;
}
.Pokedex > h3 {
  padding: 1.5rem;
  margin: 0;
  text-align: center;
}
.win {
  color: white;
  background: #76b852; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #8dc26f,
    #76b852
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.lose {
  color: white;
  background: #cb356b; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to top,
    #bd3f32,
    #cb356b
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

@media (min-width: 720px) {
  .Pokedex-item {
    margin: 0 30px;
  }
}

.Pokecard {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 200px;
  height: 290px;
  border-radius: 5px;
  font-size: 1rem;
  box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.56);
  transition: all ease 500ms;
}
.Pokecard:hover {
  transform: scale(1.1);
}

.Pokecard-item {
  line-height: 0.7rem;
  margin: 0;
  padding: 0.5rem;
  text-transform: uppercase;
}

.Pokecard-img {
  max-width: 140px;
  height: 150px;
  transition: all ease 500ms;
}
.Pokecard-img:hover {
  transform: scale(1.3);
}

